/* eslint-disable import/first */
import React from 'react';
import Langs from './components/languages/lang'
//css
import './home.css';
import Why from '../why/why';
import Pricing from '../pricing/pricing';
import Review from '../review/review';
import Projector from './components/projecter/projector.js';

const Home = () => {
        return (
            <div className="home_container">
               <Projector/>

              
                <div className = "home_title_wrapper different">
                   <h1 className="home_titlel" style = {{"color":"#ffff"}} >We code in 10+ languages</h1>
                   <h6 style = {{"color":"#E8FBFF"}}>It's hard to write code, But we do for <b>you</b></h6>
                   <Langs/>
                </div>

              
                {/* <div className="triangle_dummy">{null}</div> */}
                <div className = "home_title_wrapper">
                   <h1 className="home_titlel" >Why choose us ?</h1>
                   <h6>No one can do <b>better</b> than us</h6>
                   <Why/>
                </div>

   

                {/* <div className="triangle_2">{null}</div> */}
               <div className = "home_title_wrapper different">
                   <h1 className="home_titlel" style = {{"color":"#ffff"}} >Clients Reviews</h1>
                   <h6 style = {{"color":"#E8FBFF"}}>We don't speak about ourselves, <br/>But thier big mouths do</h6>
                   <Review/>
                </div>

              
                {/* <div className="triangle_dummy">{null}</div> */}
               <div className = "home_title_wrapper">
                   <h1 className="home_titlel" >How much we charge ?</h1>
                   <h6>Nothing is <b>free</b> on the planet earth,<br/> so we charge a little</h6>
                   <Pricing/>
               </div>
               
              
            </div>
        )
}

export default Home;