import React, { useState } from 'react';
import { Header, Icon } from 'semantic-ui-react'
import './login.css';
import SignIn from './signin/signin';
import SignUp from './signup/signup';
import PasswordReset from './password';

const Login = () => {

  const [login,setLogin] = useState(true);
  const [submitted,setSubmitted] = useState(false);
  const [passreset , setPassreset] = useState(false);
 
  const HandleToggle = (event) => {
      (event.target.name === "signin") ? setLogin(true) : setLogin(false);
  }

  const formType = (login) ? <SignIn onSUB = { ()=> setSubmitted(true) }/> : <SignUp onSUB = { ()=> setSubmitted(true) }/>;

  //const formType = <PasswordReset/>;

  const copy = (

        <Header style ={{"width":"maxContent","margin":"20px 0px 40px 0px"}} as='h2' icon>
          <Icon name='user' />
            { (login)? "Access your user account":"Create your user account" }
          <Header.Subheader>
              Make sure you read all trems and conditions
          </Header.Subheader>
        </Header>

  )

  const header = ( 
      <div className = "lb-header">
        <button name = "signin" onClick = {HandleToggle} className = {(login)?"active":""} id="login-box-link">Sign In</button>
        <button name = "signup" onClick = {HandleToggle} className = {(login)?"":"active"} id="signup-box-link">Sign Up</button>
      </div>
  );

  const content = (
        <div className = "login-box">
            { copy }
            { header }
            { formType }
       </div>
  );

  const contentIn = (
    <div className = "content_ed">
       <h6>Awesome you have {(login) ? " Signed In ":" Signed Up "} successfuly</h6>
    </div>
  );
    
   return (submitted) ? contentIn : content ;
}

export default Login;