import React from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { Button } from 'semantic-ui-react'
import HomeIcon from './home.png';
import './header.css';


const Header = () => {
    const authState = useSelector(state => state.auth);
    return (
            <div className="header_container">
            <div className="header_wrapper">
                     <div className="logo_wrapper">
                        <h1 className="logo_text">codingo</h1>
                        <div className="space"><Link to="/home"><img src={HomeIcon} alt = "loading..."/></Link></div>
                     </div>
 
                     <div className="login_buttons_container">
                           <Link to="/submit" activeclassname="active_button" className="log_button" ><Button animated = "fade" >  <Button.Content visible>Got projects?</Button.Content>
                           <Button.Content hidden>Submit</Button.Content></Button></Link>
                         {
                           (authState)?
                              <Link activeclassname="active_button" to="/my-account" className="log_button" ><Button animated = "fade" >  <Button.Content visible>My Account</Button.Content>
                              <Button.Content hidden>Let's hook up</Button.Content></Button></Link>
                            :<Link  activeclassname="active_button" to="/login" className="log_button" ><Button animated = "fade" >  <Button.Content visible>Login / Register</Button.Content>
                            <Button.Content hidden>Your Welcome</Button.Content></Button></Link>
                         }
                     </div>
            </div>
            </div>      
    )
}

export default Header;