import React, { useState , Fragment } from 'react';
import FormField from '../../formField/formField';
import { useFormik } from 'formik';
import { useDispatch } from 'react-redux';
import { setAuth } from '../../actions';
import { Button, Form , Icon} from 'semantic-ui-react';
import * as Yup from 'yup';


import './signup.css';

const SignUp = ({ onSUB }) => {
 
    const dispatch = useDispatch();

    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState('');

    const Fetch = async values => {
            setMessage('');
            setLoading(true);

            let data = new FormData();

            data.set('name', values.name);
            data.set('email', values.email);
            data.set('password', values.password);

      await fetch('api/user/signup', {
                method: 'POST',
                body:data
            })
            .then((response) =>{
              setLoading(false);
              return response.json();
            }).then((response)=>{
              if(response.Message) {
                localStorage.setItem('token', response.Message);
                console.log(response.Message);
                dispatch(setAuth(true));
                onSUB();
              }
              else {
                setMessage(response.Error);
              }
            });
    }

    

    const validationSchema = Yup.object({
       name : Yup.string().min(3).max(20).required("Required"),
       email : Yup.string().email('Invalid email addresss`').required('Required'),
       password :Yup.string()
       .min(6, 'Password has to be longer than 6 characters!')  
       .required('Password is required!')
      });


    const formik = useFormik({
      initialValues: {  
        name:'',
        email: '',
        password:''
      },
      validationSchema,
      onSubmit: values => {
          Fetch(values);
      },
    });  
    

      const Loader =(
        <Icon.Group style ={{"width":"100%","display":"flex","justifyContent":"center"}}  size='huge'>
           <Icon loading size='large' className ="loading_icon" name='circle notch' />
        </Icon.Group>
      )

        const content2 = (
          <Form onSubmit={ formik.handleSubmit } className = "signup_form">
             {
                      (loading) ? Loader:
              <Fragment>
                      <h2 className = "ses_message">{message}</h2>
                  <FormField htmlFor="name" name="name" label="Name"
                      onChange={formik.handleChange}
                      onBlur={formik.handleBlur}
                      value={formik.values.name}
                      error={
                          ( formik.touched.name && formik.errors.name )? formik.errors.name: null
                      } />

                  <FormField htmlFor="email" name="email" label="Email Address"
                      onChange={formik.handleChange}
                      onBlur={formik.handleBlur}
                      value={formik.values.email}
                      error={
                          ( formik.touched.email && formik.errors.email )? formik.errors.email: null
                      } />

                  <FormField htmlFor="password" name="password" type = "password" label="Password"
                      onChange={formik.handleChange}
                      onBlur={formik.handleBlur}
                      value={formik.values.password}
                      error={
                          ( formik.touched.password && formik.errors.password )? formik.errors.password: null
                      } />

                  <Button onClick ={ formik.handleSubmit } primary style = {{"width":"100%","height":"50px","backgroundColor":"#54c0d7","fontSize":"1.4rem","padding":"10px","fontWeight":"200","marginTop":"30px","boxSizing":"borderBox"}} type='submit'>Create Account</Button>
             
              </Fragment>
            }
          </Form>

        );
   return content2;
};

export default SignUp;