import React, { useEffect } from 'react';
import { useFormik } from 'formik';
import { withRouter } from 'react-router-dom';
import  FormField from '../formField/formField';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import * as Yup from 'yup';

import './submit.css';


const SubmitForm = ({ history }) => {
  // Pass the useFormik() hook initial form values and a submit function that will
  // be called when the form is submitted
   const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
   const FILE_SIZE = 1600000000000 * 1024;
   const SUPPORTED_FORMATS = [
     "image/jpg",
     "image/jpeg",
     "image/gif",
     "image/png"
   ];

   const validationSchema = Yup.object({

        email : Yup.string().email('Invalid email addresss`').required('Required'),
        phone : Yup.string().matches(phoneRegExp, 'Phone number is not valid').required("Required"),
        deadline: Yup.date().required("Required"),
        detail : Yup.string().max(400).required("Required"),
        file: Yup.mixed().required("A file is required")

   });


  const SubmitFetch =async values => {
    const data = new FormData();

    data.append('file', values.file);
    data.append('email1', values.email);
    data.append('phone', values.phone);
    data.append('deadline', values.deadline);
    data.append('detail', values.detail);
    data.append('lang', values.lang);
    data.append('type', values.type);

    await fetch('api/submit/form',{
              method: 'POST',
              headers: {
                'x-access-token':localStorage.getItem('token')},
              body:data
            })
            .then((response) =>{
                if(response.status === 401)
                  this.setState({loading:false,message:"Please login first to Submit!!"});
                else if(response.status === 503)
                  this.setState({loading:false,message:"Unable to submit (Server error)"})
                else if(response.status === 200)
                  history.replace('/submitted');
            });
  }


  const formik = useFormik({
    initialValues: {      
      email: '',
      phone:'',
      deadline:'',  
      file:'',
      type:'',
      lang : '',
      detail :''
    },
    validationSchema,
    onSubmit: values => {
        SubmitFetch(values);
    },
  });  
  
  return (
    <div className = "su_container">
    <form onSubmit={ formik.handleSubmit } className = "form_submit">
            <h1 className = "_contact_form_h1">Submit</h1>
            <p  className = "_contact_form_h2">Submit your final projects and assignments <br/>
            Please provide valid information</p>

             <FormField for="email" label="Email" type="email" name="email" 
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
                value={formik.values.email}
                error={
                    ( formik.touched.email && formik.errors.email )? formik.errors.email: null
                }
            />

            <FormField for="phone" label="Phone" type="no." name="phone" 
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
                value={formik.values.phone}
                error={
                    ( formik.touched.phone && formik.errors.phone )? 
                            formik.errors.phone : null
                }
            />

            <FormField for="deadline" label="Deadline" type="date" name="deadline" 
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
                value={formik.values.deadline}
                error={
                    ( formik.touched.deadline && formik.errors.deadline )? formik.errors.deadline: null
                }
            />

            <div className = "submit_form_label_wrapper">
            <label>Type</label>
                <select name="type" onChange={formik.handleChange} value={formik.values.type}>
                            <option  defaultValue value = {undefined} disabled >Select Type</option>
                            <option value="ho" >Home Assignment</option>
                            <option value="fp" >Final Project</option>
                </select>
                {formik.errors.lang ? <div>{formik.errors.type}</div> : null}
            </div>


            <div className="submit_form_label_wrapper">
                <label>Language</label>
                    <select name="lang" onChange={formik.handleChange} value={formik.values.lang}>
                        <option selected = { true } defaultValue value = {undefined} disabled = {true} >Select Language</option>
                        <option value="javascript" >Javascript</option>
                        <option value="java" >Java</option>
                        <option value="c" >C</option>
                        <option value="c++" >C++</option>
                        <option value="python" >Python</option>
                    </select>
                {formik.errors.lang ? <div>{formik.errors.type}</div> : null}
            </div>


            <FormField for="File" label="Upload File" className="dropzone" type="file" name="file" 
                onChange={(event) => formik.setFieldValue("file", event.currentTarget.files[0]) }
                onBlur={formik.handleBlur}
                // value={formik.values.file}
                error={
                    ( formik.touched.file && formik.errors.file )? formik.errors.file: null
                }
            />

            <div className="submit_form_label_wrapper">
                <label>Detailed Description of Project</label>
                <textarea
                    name="detail"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.detail}
                    type="text"
                />
                {formik.errors.lang ? <div>{formik.errors.lang}</div> : null}
            </div>
            <button type="submit" onClick ={formik.handleSubmit } className = "auto_submit_button">Submit</button>
    </form>
   
    </div>
    )
};

export default withRouter(SubmitForm);