import React from 'react';
import { Link } from 'react-router-dom';
import Verified from './verified.svg';
import { Button } from 'semantic-ui-react'
import './_pricing.css';

const Pricing = () => {
  
	const content_2 = (
		<div className = "_pricing_container">
				<div className = "_pricing_wrapper">

					<div className  ="_plan_container _plan_1">
						<h1 className = "_plan_header">Assignment</h1>
						<p className = "_plan_pricing_box"><span><s>$45</s></span>$23</p>
						<ul className = "_plan_detail">
							<li><h1>Home Assignment</h1></li>
						</ul>
						<Link to="/submit" className = "_pricing_cta_link"><Button className = "_pricing_cta" content='Submit' /></Link>
						
					</div>

					<div className  ="_plan_container _plan_2">
						<h1 className = "_plan_header">Final Project</h1>
						<p className = "_plan_pricing_box"><span><s>$250</s></span>$119</p>
						<ul className = "_plan_detail">
							<li><h1>Final Project</h1></li>
						</ul>
						<Link to="/submit"  className = "_pricing_cta_link"><Button className = "_pricing_cta" content='Submit' /></Link>
					</div>

					<div className  ="_plan_container _plan_3">
						<h1 className = "_plan_header">Web or App</h1>
						<p className = "_plan_pricing_box">$ as per your project.</p>
						<ul className = "_plan_detail">
							<li><h1>Website or App</h1></li>
						</ul>
						<Link to="/contact"  className = "_pricing_cta_link"><Button className = "_pricing_cta" content='Submit' /></Link>
					</div>

				</div>
		</div>
	)
	return content_2;
}

export default Pricing;
