import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { Header , List , Form , Button , Icon } from 'semantic-ui-react';
import './footer.css';

const Footer = () => {
    const authState = useSelector(state => state.auth);
    const [message , setMessage] = useState("");
    return(
      <div className="footer_container">
          <div className="footer_wrapper">
               <div className = "footer_section">
                  
                        <List size = "huge">
                            <h1 style ={{"textAlign":"left","color":"white"}} size="large" className="logo_text">Codingo</h1>
                            
                            <List.Item>
                                <List.Icon name='users' />
                                <List.Content>Projectodia Ltd.</List.Content>
                            </List.Item>

                            <List.Item>
                                <List.Icon name='marker' />
                                <List.Content> Jalandhar, Punjab(India)</List.Content>
                            </List.Item>

                            <List.Item>
                                <List.Icon name='mail' />
                                <List.Content>
                                    <Link href='mailto:jack@semantic-ui.com'>projectodiaofficial@gmail.com</Link>
                                </List.Content>
                            </List.Item>

                            <List.Item>
                                <List.Icon name='linkify' />
                                <List.Content>
                                    <Link href='http://www.semantic-ui.com'>projectodia.com</Link>
                                </List.Content>
                            </List.Item>

                        </List>

               </div>

               <div className = "footer_section">
                    
                    <List size = "big">
                      <Header  as = "h3" style ={{"textAlign":"left"}}>Quick Links</Header>
                        <List.Item>
                            <Link to="/home">Home</Link>
                        </List.Item>
                        <List.Item>
                            <Link to="/submit">Submit</Link>
                        </List.Item>
                        {
                          (authState)? <List.Item>
                                         <Link to="/my-account">My Account</Link>
                                       </List.Item> : 
                                       <List.Item>
                                           <Link to="/login">Login/Register</Link>
                                       </List.Item> 
                        }
                        <List.Item>
                            <Link to="/about">Our Story</Link>
                        </List.Item>
                        <List.Item>
                            <Link to="/policy">Service and Policy</Link>
                        </List.Item>
                    </List>

               </div>

               <div className = "footer_section">
                    <List size = "big">
                      <Header  as = "h3" style ={{"textAlign":"left"}}>Newsletter Sign Up</Header>
                        <List.Item>  
                            <Form.Field style = {{"display":"flex","flex-direction":"column"}}>
                                <label className = "lables_">Email Address</label>
                                <input className = "inputs_" value = {message} onChange = {(e)=>setMessage(e.currentTarget.value)} placeholder='xyz@abc.com' />
                            </Form.Field>
                        </List.Item>
                        <List.Item><Button primary style = {{"backgroundColor":"#54c0d7"}}>Submit</Button></List.Item>
                    </List>
               </div>

               <div className = "footer_section">
                    <List horizontal size = "massive">
                      <Header  as = "h3" style ={{"textAlign":"left"}}>Follow Us</Header>
                        <List.Item>  
                            <Icon size = "large" circular color = "pink" name = "instagram"/>
                        </List.Item>
                        <List.Item>
                            <Icon size = "large" circular color = "blue" name = "facebook"/>
                        </List.Item>
                        <List.Item>
                            <Icon size = "large" circular color = "blue" name = "linkedin"/>
                        </List.Item>
                    </List>
               </div>
          </div>
          <div className = "footer_copy_wrapper">
              <Header style ={{"width":"100%","textAlign":"center"}} as = "h3">Privacy Policy | Terms of Use</Header>
              <Header style ={{"width":"100%","textAlign":"center"}} as = "h5" > 2019 Projectodia, Ltd. All rights reserved.</Header>
          </div>
      </div>
    )

};

export default Footer;
