import React ,{ useState , useEffect } from 'react';
import { Switch, Route  } from 'react-router-dom';
import { useDispatch , useSelector } from 'react-redux';
import { authReducer } from './reducers';
import { setAuth } from './actions';
import Home from './home/home';
import Header from './header/header';
import Login from './login/login';
import MyAccount from './account/myaccount';
import ThankYouSubmit from './submit/than-you';
import { Elements } from 'react-stripe-elements';
import SubmitPage from './submit/main';
import Footer from './footer/footer';
import PasswordReset from './login/password'
import 'semantic-ui-css/semantic.min.css'
import { StripeProvider } from 'react-stripe-elements';
import Contact from './contact/contact';

import CheckoutForm from './checkout/3d_secure';

const App = () => {
        const dispatch = useDispatch();
        const authState = useSelector(state => state.auth);
        const [stripe , setStripe] = useState(null);


        useEffect(()=>{
            if(localStorage.getItem('token')){
                fetch('/api/user/verify', {
                    method: 'POST',
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                        'x-access-token':localStorage.getItem('token')
                    },
                    body:""
                })
                .then( (response) => response.json() )
                .then( (responseJson) => dispatch( setAuth( responseJson.valid ) ) )
            }
            else
                dispatch(setAuth(false));
    },[]);

    useEffect(() => {
        if (window.Stripe)
                setStripe(window.Stripe('pk_test_sfQ4hv8GtQbqYq1A5kR56PTw00q8o2d9DU'));
        else {
            if(document.querySelector('#stripe-js')){
                document.querySelector('#stripe-js').addEventListener('load', () => {
                    // Create Stripe instance once Stripe.js loads
                    setStripe(window.Stripe('pk_test_sfQ4hv8GtQbqYq1A5kR56PTw00q8o2d9DU'));
                });
            }
        }
    }, [document.querySelector('#stripe-js')]);

      
        
        return (
            <StripeProvider stripe={ stripe }>
                <div className="app_container">
                    <Header/>
                        <Switch>
                            <Route exact path="/" exact component={Home}/>
                            <Route exact path="/home" exact component={Home}/>
                            <Route exact path="/submit" exact component={SubmitPage}/>
                            <Route exact path="/login" exact component={Login}/>
                            <Route exact path="/my-account" component={MyAccount}/>
                            <Route exact path="/contact" component={Contact}/>
                            <Route exact path="/submitted" component={ThankYouSubmit}/>
                            <Route exact path="/checkout/:id" render={(props)=><Elements><CheckoutForm {...props}/></Elements>}/>
                            <Route exact path="/password-reset" component={PasswordReset}/>
                        </Switch>
                    <Footer/>
                </div>
            </StripeProvider>
        )
};

export default App;
