import React from 'react';
import './why.css';

import Plag from './svg/plagi.svg';
import Quality from './svg/quality.svg';
import Time from './svg/time.svg';

const Why = () => {
    return (
    <div
           className="why_container">

            <div
                className="why_reason_body">
                <img
                    src={Plag}
                    className="why_reason_icon"
                    alt = "loading..."/>
                    <h2 className="why_reason_text">Plagiarism Free</h2>
            </div>

            <div 
                className="why_reason_body">
                <img src={Time} 
                    className="why_reason_icon"
                    alt = "loading..."/>
                    <h2 className="why_reason_text">On Time Delivary</h2>
            </div>

            <div 
                className="why_reason_body">
                <img src={Quality} 
                    className="why_reason_icon"
                    alt = "loading..."/>
                    <h2 className="why_reason_text">Quality Assurance</h2>
            </div>

    </div>
    )
}

export default Why;