import React, { useState , Fragment } from 'react';
import FormField from '../formField/formField';
import { useFormik } from 'formik';
import { useDispatch } from 'react-redux';
import { Button, Form , Icon} from 'semantic-ui-react';
import * as Yup from 'yup';


import './signup/signup.css';

const PasswordReset = () => {
 
    const dispatch = useDispatch();

    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState('');

    const Fetch = async values => {
            setLoading(true);

            let data = new FormData();

            data.set('email', values.email);

      await fetch('api/user/password-reset-request', {
                method: 'POST',
                body:data
            })
            .then((response) =>{
                  switch (response.status) {
                    case 401:
                      setMessage("Please Check Password/Email");
                    break;

                    case 402:
                      setMessage("User already exists");
                    break;

                    case 404:
                      setMessage("Not Found");
                    break;

                    case 503:
                      setMessage("Server error");
                    break;

                    case 400:
                      setMessage("Please Enter Valid Password/Email");
                    break;

                    case 200:
                      setMessage("changed");
                    break;

                  };
              setLoading(false);
              return response.json();
            })
    }

    

    const validationSchema = Yup.object({
       email : Yup.string().email('Invalid email addresss`').required('Required'),
      });


    const formik = useFormik({
      initialValues: {  
        email: '',
      },
      validationSchema,
      onSubmit: values => {
          Fetch(values);
      },
    });  
    

      const Loader =(
        <Icon.Group style ={{"width":"100%","display":"flex","justifyContent":"center"}}  size='huge'>
           <Icon loading size='large' className ="loading_icon" name='circle notch' />
        </Icon.Group>
      )

        const content2 = (
          <Form onSubmit={ formik.handleSubmit } style = {{"max-width":"500px","minHeight":"400px","display":"block","margin":"auto","marginTop":"100px"}} className = "signup_form">
             {
                      (loading) ? Loader:
              <Fragment>

                  <FormField htmlFor="email" name="email" label="Email Address"
                      onChange={formik.handleChange}
                      onBlur={formik.handleBlur}
                      value={formik.values.email}
                      error={
                          ( formik.touched.email && formik.errors.email )? formik.errors.email: null
                      } />

                  <Button onClick ={ formik.handleSubmit } primary style = {{"width":"100%","height":"50px","backgroundColor":"#54c0d7","fontSize":"1.4rem","padding":"10px","fontWeight":"200","marginTop":"30px","boxSizing":"borderBox"}} type='submit'>Send Me Verification Email</Button>
             
              </Fragment>
            }
          </Form>

        );
    if (message === "changed") {
        return <h1>A link has been sent to your Email Address</h1>
    }
   return content2;
};

export default PasswordReset;